import React from "react";
import { Layout } from "antd";
import { Link } from "react-router-dom";

import TopNavigation from "../navigation/TopNavigation";

const HomePage = () => (
	<div
		style={{
			minHeight: "100vh"
		}}
	>
		<Layout>
			<TopNavigation />
			<div
				style={{
					color: "#F3ECFF",
					textAlign: "center",
					//					background: `url(${background1}) no-repeat center center fixed`,
					backgroundSize: "cover",
					filter: "blur(2px)",
					background: "white"
				}}
				className="home__background"
			/>
			<div
				style={{
					position: "absolute",
					top: "50%",
					right: "50%",
					zIndex: 1,
					transform: "translate(50%, -25%)"
				}}
			>
				<p
					style={{
						fontSize: "8vmax",
						textAlign: "-webkit-auto",
						fontWeight: "400",
						marginBottom: "1vmax"
					}}
				>
					crazy sale
				</p>

				<Link to="/shop">
					<div
						style={{
							padding: "1em"
						}}
					>
						<p
							style={{
								fontSize: "3vmax",
								fontWeight: "200",
								border: "1px solid",
								width: "fit-content",
								padding: "1vmax"
							}}
						>
							SHOP NOW
						</p>
					</div>
				</Link>
			</div>
			<div
				style={{
					position: "absolute",
					top: "40%",
					right: "15%",
					height: "30vmax",
					width: "30vmax",
					background: "#ffebdae0",
					zIndex: 0
				}}
			/>
		</Layout>
	</div>
);

export default HomePage;



