import React from "react";
import { Menu, Icon, Button, Row } from "antd";
import { Link } from "react-router-dom";

class TopNavigation extends React.Component {
	state = {
		current: "mail",
		token: false,
		tokenRole: ""
	};
	componentDidMount = () => {
		const token = localStorage.getItem("token");
		if (token) {
			this.setState({
				token: true,
				tokenRole: token.split(" ")[1]
			});
		}
	};
	handleClick = e => {
		console.log("click ", e);
		this.setState({
			current: e.key
		});
	};
	onClickLogOut = () => {
		localStorage.removeItem("token");
	};
	render() {
		return (
			<div
				style={{
					position: "relative",
					width: "100%",
					zIndex: "10",
					backgroundColor: "white"
				}}
			>
				<Row type="flex" justify="center" align="middle">
					<h1 className="textLogo">JELERY SHOP</h1>
				</Row>
				<Menu
					onClick={this.handleClick}
					selectedKeys={[this.state.current]}
					mode="horizontal"
					style={{
						width: "fit-content",
						margin: "auto"
					}}
				>
					<Menu.Item key="home">
						<Link to="/">
							<Icon type="home" />Home
						</Link>
					</Menu.Item>
					<Menu.Item key="shop">
						<Link to="/shop">
							<Icon type="shop" />SHOP
						</Link>
					</Menu.Item>
					{this.state.token === true ? (
						<Menu.Item key="app">
							{this.state.tokenRole === "user" ? (
								<Link to="/user">
									<Icon type="user" />My Cart
								</Link>
							) : null}
							{this.state.tokenRole === "admin" ? (
								<Link to="/dashboard">
									<Icon type="dashboard" />Dash Board
								</Link>
							) : null}
						</Menu.Item>
					) : (
						""
					)}
					{this.state.token === true ? (
						""
					) : (
						<Menu.Item key="Sign_In" style={{}}>
							<Link to="/login">
								<Button>
									<Icon type="login" /> Login
								</Button>
							</Link>
						</Menu.Item>
					)}
					{this.state.token === true ? (
						<Menu.Item key="Log_out" style={{}}>
							<a href="/" onClick={this.onClickLogOut}>
								<Button type="primary">Logout</Button>
							</a>
						</Menu.Item>
					) : (
						<Menu.Item key="Sign_up" style={{}}>
							<Link to="/signup">
								<Button type="primary">Sign up</Button>
							</Link>
						</Menu.Item>
					)}

					<Menu.Item key="shopping-cart">
						<Link to="/cart">
							<Icon type="shopping-cart" />
							Shopping Cart
						</Link>
					</Menu.Item>
				</Menu>
			</div>
		);
	}
}

export default TopNavigation;



